import React, { Component } from 'react';
import HeadShake from 'react-reveal/HeadShake';
class Contact extends Component {
  render() {

    if(this.props.data){
      var name = this.props.data.name;
      var profilepic= "images/"+this.props.data.image;
      var bio = this.props.data.bio;
      
      var city = this.props.data.address.city;
      var state = this.props.data.address.state;
    
      var phone= this.props.data.phone;
      var email = this.props.data.email;
      var resumeDownload = "https://drive.google.com/drive/folders/1cR9i8NAo78fYtuHTvroYlOsQ8tvdJCUU?usp=sharing" ;
    }

    return (
      <section id="contact">
  <div className="row">
         <div className="three columns">
            <img className="profile-pic"  src={profilepic} alt="Vasuprada Profile Pic" />
         </div>
         <div className="nine columns main-col">
           <div className="columns contact-details">
                  <h2>Contact Details</h2>
                  <p className="address">
						   <span>{name}</span>
						   <span><br />
						         {city},{state}
                   </span>
						   <span>{phone}</span><br />
                     <span>{email}</span>
					   </p>
             <p>
                    <HeadShake> <a href={resumeDownload} className="button"><i className="fa fa-download"></i> &nbsp; Resume</a></HeadShake>
                  </p>
               </div>
               <br />
            
            
            <div className="row">
               
               <div className="columns download">
                  
               </div>
            </div>
         </div>
      </div>
   </section>
    );
  }
}

export default Contact;
