import React from 'react';
import { makeStyles } from '@material-ui/core/styles';
import Accordion from '@material-ui/core/Accordion';
import AccordionSummary from '@material-ui/core/AccordionSummary';
import AccordionDetails from '@material-ui/core/AccordionDetails';
import Typography from '@material-ui/core/Typography';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import '../custom.css'
const useStyles = makeStyles((theme) => ({
  root: {
    width: '100%',
    font:'librebaskerville-italic',
    fontSize:'15px'
  },
  heading: {
    fontSize: theme.typography.pxToRem(15),
    fontWeight: theme.typography.fontWeightRegular,
  },
  body1:{
    font:'librebaskerville-italic',
  }
}));

export default function SimpleAccordion() {
  const classes = useStyles();

  return (
    <div className={classes.root}>
    <p className="info">ISRO<span>&bull;</span><em className="date"></em></p>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <p className="acheading">Developed Open Source Offline Routing Android Application - 8th Semester Dissertation - IIRS, ISRO, 2019.</p>
        </AccordionSummary>
        <AccordionDetails>
          <p  className="acbody">
          An application development with complete offline
support from open source tools and softwares. Many of the location-based applications
which provide services like Routing, Directions, Navigation work on an online basis, restrict
the usage of those applications in ill network connectivity areas. All these applications
require cellular connectivity and an active data connection. At the hour of need, there would
be no guarantee for the network and data availability which end up worthless. The demand
for offline applications rises at time of casualty. The application directly depends on the data
stored in its local storage and can be updated once in a while with good network availability.
This application allows the user to gain route offline and search among the POI’s based on
entered keywords. Many libraries, frameworks, tools, Data providers, plugins are chosen.
Secondly, All the data that is to be needed offline, is developed, and manipulated, stored in
the external storage. The UI of the application is developed as per the requirements and
additional functionalities are added. The scope of the application for different platforms is
also considered. Based on the findings about the different open source offline routing
providers and map providers the application is developed.
          </p>
        </AccordionDetails>
      </Accordion>
      <p className="info">ESRI<span>&bull;</span><em className="date"></em></p>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <p className="acheading">Your Ally - Developed Society Management Application end-to-end full stack and presented in ESRI UC, 2018 as top 5 finalists.</p>
        </AccordionSummary>
        <AccordionDetails>
          <p  className="acbody">
          Your Ally is an Emergency Response Management Application designed and
developed for Societies. Mobile Applications have driven people towards a better
living, better livelihood, and better future. There have been many developments
regarding the emergency response management like police, health care
(ambulances, hospitals etc.). But any of these will take at least a considerable
time of 5- 10 minutes or more to help people and reach them in case of
emergency. Sometimes, this may end people in loss.
So in this application, people will be profited from their very own societies and
neighborhoods. This app bridges the delay in emergency cases. Their neighbors
are those who get emergency alerts and apart from this, quick aid services like
police, ambulance services can also be integrated.
The very first step is where individuals get registered. The spatial coordinates and
details of the user will be entered into the database. After multiple registrations,
a buffer value (variable) can be given by the user so as to note the neighboring
buildings. At the time of casualty, when the user clicks the alert notification, all
the people registered, closest to the user will be alerted and alarmed about the
help required.The route map is also provided for the receiver for odd scenarios
and better efficiency. The application is developed on an Android Platform with
the help of ESRI’s GIS technologies.The application made use of ArcGIS
Online,ArcGIS - WebAppBuilder for Developers, ArcGIS API For JavaScript,
Firebase Authentication,Hosting and Cloud Functions, Node JS for back-end
scripting.
This application can be used anywhere by anyone given that if the neighbors have
this application too
Every individual house may or may not have police or health service nearby, but
before all this every house has a neighbor, A neighbor who can help them in time
of need.
          </p>
        </AccordionDetails>
      </Accordion>
      <p className="info">ESRI<span>&bull;</span><em className="date"></em></p>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <p className="acheading">Varun Raksha - Developed Disaster Management Application end-to-end and presented in ESRI UC, 2017 as top 7 finalists.</p>
        </AccordionSummary>
        <AccordionDetails>
          <p  className="acbody">
          The prime intention of this app is to alert people in case of disasters like cyclone .
.Making people aware of the upcoming disaster and helping
them take good measures helps in decrease of their panic and hence decrease of life loss.A
helping hand is what people require.
Winds can be predicted hours before. So in off chance an organization provides data regarding
the delineation of risk and the safe zones .There would be an update of our database and
eventually displayed to the user.In case if the user lies in the risk zone he/she could request for
routing , and henceforth routing to the closest safe zone will be provided.Apart from this ,if
he/she knew that his companions also lies in the same risk zone he could access the contacts
and intimate them by the app and the default message will be sent.By this,Even people without
our app can be benefited.
Users can as well register their phone numbers and pin codes so that they could receive time to
time update in the disaster period.Pin codes which fall under the disaster zone will be recovered
and messages will be sent accordingly to the users.
Our app is a Prototype Model and is Trail - based.The risk zones and safe zones are done
purely for demo purpose.
A Satellite imagery Of HUD - HUD cyclone on 11th of October also helped us in distinguishing​ the
areas.And for Real-Time database we used Firebase.
          </p>
        </AccordionDetails>
      </Accordion>
      <p className="info">APSAC<span>&bull;</span><em className="date"></em></p>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <p className="acheading">Developed Geotagging WebGIS Application to APSAC, Vijayawada, 2017</p>
        </AccordionSummary>
        <AccordionDetails>
          <p className="acbody">
          Using the given data of various Assets like Education, Health, Sanitation and Sewerage
Facilities, Banking and Credit facilities, General Public and Social Assets, Tourist and
Recreational Facilities, Public Distribution and Collection Centers, Transport Facilities
and Drinking Water Facilities their functionality and availability are plotted and the
locations are geotagged to the Basemap. Maintaining an up-to-date database helps in
getting map data dynamically. The Basemap and the Overlays can be changed as per
requirement. This application provides the basic assessment of the assets regarding
facilities like playground, toilet, physical condition, service, asset functionality etc. This
application is compatible to become a part of bigger Asset Mapping webpages.
          </p>
        </AccordionDetails>
      </Accordion>
      
    </div>
  );
}
